import { subjectManagerApi } from "./subjectManagerApi";
import {
  subjectTabControllerFunction,
  subjectTabManageFunction,
  subjectAddCatalogStudentFunction,
  subjectRemoveCatalogStudentFunction,
  subjectAddTimetableFunction,
  subjectUpdateTimetableFunction,
  subjectRemoveTimetableFunction,
  getSubjectOneDayTimetableFunction,
  getFeedStaffCurrentDayScheduleFunction,
  getInternalEvaluationListFunction,
  addInternalEvaluationFunction,
  updateInternalEvaluationFunction,
  removeInternalEvaluationFunction,
  getInternalEvaluationStudentListFunction,
  getInternalEvaluationTestListFunction,
  getInternalEvaluationTestStudentListFunction,
  addInternalEvaluationTestFunction,
  updateInternalEvaluationTestFunction,
  removeInternalEvaluationTestFunction,
  markGiveInternalEvaluationTestFunction,
  internalEvaluationExportStudentFunction,
} from "./subject_manager_query_mutation_function";

const subjectManagerApiDynamic = subjectManagerApi.injectEndpoints({
  endpoints: (builder) => ({
    subjectTabManage: subjectTabManageFunction(builder),
    subjectTabController: subjectTabControllerFunction(builder),
    subjectAddCatalogStudent: subjectAddCatalogStudentFunction(builder),
    subjectRemoveCatalogStudent: subjectRemoveCatalogStudentFunction(builder),

    subjectAddTimetable: subjectAddTimetableFunction(builder),
    subjectUpdateTimetable: subjectUpdateTimetableFunction(builder),
    subjectRemoveTimetable: subjectRemoveTimetableFunction(builder),
    getSubjectOneDayTimetable: getSubjectOneDayTimetableFunction(builder),
    getFeedStaffCurrentDaySchedule:
      getFeedStaffCurrentDayScheduleFunction(builder),

    getInternalEvaluationList: getInternalEvaluationListFunction(builder),
    addInternalEvaluation: addInternalEvaluationFunction(builder),
    updateInternalEvaluation: updateInternalEvaluationFunction(builder),
    removeInternalEvaluation: removeInternalEvaluationFunction(builder),
    getInternalEvaluationStudentList:
      getInternalEvaluationStudentListFunction(builder),
    getInternalEvaluationTestList:
      getInternalEvaluationTestListFunction(builder),
    getInternalEvaluationTestStudentList:
      getInternalEvaluationTestStudentListFunction(builder),
    addInternalEvaluationTest: addInternalEvaluationTestFunction(builder),
    updateInternalEvaluationTest: updateInternalEvaluationTestFunction(builder),
    removeInternalEvaluationTest: removeInternalEvaluationTestFunction(builder),
    markGiveInternalEvaluationTest:
      markGiveInternalEvaluationTestFunction(builder),
    internalEvaluationExportStudent:
      internalEvaluationExportStudentFunction(builder),
  }),
});
export const {
  useSubjectTabManageQuery,
  useSubjectTabControllerMutation,
  useSubjectAddCatalogStudentMutation,
  useSubjectRemoveCatalogStudentMutation,

  useSubjectAddTimetableMutation,
  useSubjectUpdateTimetableMutation,
  useSubjectRemoveTimetableMutation,
  useGetSubjectOneDayTimetableQuery,
  useGetFeedStaffCurrentDayScheduleQuery,
  //
  useGetInternalEvaluationListQuery,
  useAddInternalEvaluationMutation,
  useUpdateInternalEvaluationMutation,
  useRemoveInternalEvaluationMutation,
  useGetInternalEvaluationStudentListQuery,
  useGetInternalEvaluationTestListQuery,
  useGetInternalEvaluationTestStudentListQuery,
  useAddInternalEvaluationTestMutation,
  useUpdateInternalEvaluationTestMutation,
  useRemoveInternalEvaluationTestMutation,
  useMarkGiveInternalEvaluationTestMutation,
  useInternalEvaluationExportStudentMutation,
} = subjectManagerApiDynamic;
