import { useState } from "react";
import AddButtonWrapper from "../../Ui/Button/AddButtonWrapper";
import CreateButton from "../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import AddFeedSubjectTimetableDetailDayWise from "./AddFeedSubjectTimetableDetailDayWise";
import { useGetSubjectOneDayTimetable } from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import style from "../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import FeedSubjectTimetableDetailDayWiseMenu from "./FeedSubjectTimetableDetailDayWiseMenu";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
const FeedSubjectTimetableDetailDayWise = ({ day, subjectId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [open, setOpen] = useState("");
  const {
    getSubjectOneDayTimetable,
    getSubjectOneDayTimetableLoading,
    getSubjectOneDayTimetableRefetch,
  } = useGetSubjectOneDayTimetable({
    data: {
      sid: subjectId,
      day: day,
    },
    skip: !subjectId,
  });
  const onAdd = () => {
    setAdd((pre) => !pre);
  };
  const onOpen = () => {
    setOpen("");
  };
  return (
    <>
      <AddButtonWrapper>
        <CreateButton
          label="add_time_table"
          customStyle={{
            padding: "0.4rem 1rem",
            margin: "0",
          }}
          onAction={onAdd}
        />
      </AddButtonWrapper>

      {getSubjectOneDayTimetable?.schedule?.map((t_t) => (
        <div className={style.sf_card} key={t_t?._id}>
          <div className={style.sf_card_contatiner}>
            <img
              src="/images/member_tab/class/class-time-icon.svg"
              loading="lazy"
              alt="time table icon"
              style={{
                width: "1.5rem",
                height: "1.5rem",
              }}
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {t_t?.from} To {t_t?.to}
              </h6>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={() => setOpen(t_t)}
          />
        </div>
      ))}
      {getSubjectOneDayTimetable?.schedule?.length === 0 && (
        <EmptyMessage
          customStyleContainer={{
            marginTop: "1.5rem",
          }}
          title="No timetable added."
        />
      )}

      {getSubjectOneDayTimetableLoading && <Loading />}
      {add && (
        <AddFeedSubjectTimetableDetailDayWise
          subjectId={subjectId}
          onClose={onAdd}
          day={day}
          onRefetch={getSubjectOneDayTimetableRefetch}
        />
      )}

      {open && (
        <FeedSubjectTimetableDetailDayWiseMenu
          onClose={onOpen}
          menuData={open}
          day={day}
          onRefetch={getSubjectOneDayTimetableRefetch}
          ddid={getSubjectOneDayTimetable?._id}
        />
      )}
    </>
  );
};

export default FeedSubjectTimetableDetailDayWise;
